*,*::before,*::after{
  box-sizing: border-box;
}
body{
  font-family:"Roboto",'Noto Sans TC', sans-serif,serif;
  
}

// a連結設定& 動畫
a{
  display: block;
  text-decoration: none;
  color: black;
}

@keyframes opacity{
  0%{
    opacity: 0.5;
  }
  100%{
    
    opacity: 1;
  }
}	
@keyframes text{
  0% {
    transform: translateY(0);
  }
  100% {
    transform: scale(1.2);
    color: yellow;
    
  }
}


img{
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

